import axios from "axios";

const apiClient=axios.create(
    {
        baseURL: 'https://leftoverlove32721.up.railway.app'
    }
);

export function retrieveAllUsers(){
    return apiClient.get('/users')
}

export function createUser(user){
    return apiClient.post('/users',user)
}

export function donateFood(id,food){
    return apiClient.post(`/users/${id}/donations`,food)
}

export function getDonations(id){
    return apiClient.get(`/users/${id}/donations`)
}

export function getFoods(){
    return apiClient.get('/foods')
}

export function getFoodById(id){
    return apiClient.get(`/foods/${id}`)
}

export function getFoodDonor(id){
    return apiClient.get(`/foods/${id}/donor`)
}

export function getFoodOrders(id){
    return apiClient.get(`/foods/${id}/orders`)
}

export function orderFood(id,food){
    return apiClient.post(`/users/${id}/orders`,food)
}

export function getOrders(id){
    return apiClient.get(`/users/${id}/orders`)
}