import { useState } from "react";
import './css/donateitems.css'
import { useAuth } from "./AuthContext";
import { donateFood } from "./api/ApiService";

export default function Donateitems() {
    const [foodName, setFoodName] = useState('');
    const [expiryDate, setExpiryDate] = useState('');
    const [image,setImage]=useState('')
    const [quantity,setQuantity]=useState(null)
    const [errorMessage,setErrorMessage]=useState('')
    const [showError,setShowError]=useState(false)
    const [color,setColor]=useState('green')
    const auth=useAuth()

    const handleSubmit = (e) => {
        e.preventDefault();
        console.log("Food Name:", foodName);
        console.log("Expiry Date:", expiryDate);
        const food={
            name:foodName,
            image:image,
            quantity:quantity,
            expirydate:expiryDate
        }
        donateFood(auth.id,food)
            .then((response)=>success(response))
            .catch((error)=>failure(error))
        setFoodName('');
        setExpiryDate('');
        setImage('');
    };

    function success(response){
        setShowError(true)
        setErrorMessage('Donated Successfully')
        setColor('green')
    }

    function failure(error){
        setShowError(true)
        setErrorMessage(error.response.data.message)
        setColor('red')
        console.log(error)
    }

    return (
        <div className="main-c">
            <div className="form-container">
                {showError&&<h3 style={{color:color}}>{errorMessage}</h3>}
                <h2>Food Donation Form</h2>
                <form onSubmit={handleSubmit}>
                    <div className="form-group">
                        <label>Food Name:</label>
                        <input
                            type="text"
                            value={foodName}
                            onChange={(e) => setFoodName(e.target.value)}
                            required
                        />
                    </div>
                    <div className="form-group">
                        <label>Image :</label>
                        <input
                            type="text"
                            value={image}
                            onChange={(e)=>setImage(e.target.value)}
                            required
                        />
                    </div>
                    <div className="form-group">
                        <label>Quantity(grams) :</label>
                        <input
                            type="number"
                            value={quantity===null?'':quantity}
                            onChange={(e)=>setQuantity(e.target.value===''?null:parseInt(e.target.value))}
                            required
                        />
                    </div>
                    <div className="form-group">
                        <label>Expiry Date:</label>
                        <input
                            type="date"
                            value={expiryDate}
                            onChange={(e) => setExpiryDate(e.target.value)}
                            required
                        />
                    </div>
                    <button type="submit" className="submit-button">Donate</button>
                </form>
            </div>
        </div>
    )
}