import { useEffect, useState } from 'react';
import FooditemCard from './FooditemCard';
import { getDonations, getFoodDonor,getFoodOrders, getFoods, orderFood } from './api/ApiService';
import { useAuth } from './AuthContext';

export default function Fooditems() {
    const auth=useAuth()
    const [items,setItems]=useState([])
    const [selectedItem,setSelectedItem]=useState(null)
    const [address,setAddress]=useState('')
    const [ditems,setditems]=useState([])
    const [name,setName]=useState('')

    useEffect(()=>{
        getDonations(auth.id)
            .then((response)=>setditems(response.data.map(item=>item.id)))
    },[auth.id])

    /*useEffect(()=>{
        getFoods()
            .then((response)=>{
                const curdate=new Date()
                curdate.setHours(0,0,0,0)
                const validitems=response.data
                    .filter(item=>!ditems.includes(item.id))
                    .filter(item=>{
                        const itemexp=new Date(item.expirydate)
                        itemexp.setHours(0,0,0,0)
                        return itemexp>=curdate
                    })
                    
                setItems(validitems)
            })
            .catch((error)=>console.log(error))
    },[ditems])*/

    useEffect(() => {
        getFoods()
            .then(async (response) => {
                const curdate = new Date();
                curdate.setHours(0, 0, 0, 0);
                const validItems = [];
                for (const item of response.data) {
                    const foodOrders = await getFoodOrders(item.id);
                    if (foodOrders.data.length === 0 && !ditems.includes(item.id)) {
                        const itemExp = new Date(item.expirydate);
                        itemExp.setHours(0, 0, 0, 0);
                        if (itemExp >= curdate) {
                            validItems.push(item);
                        }
                    }
                }

                setItems(validItems);
            })
            .catch((error) => console.log(error));
    }, [ditems, auth.id]);

    const handleOrder = async (item) => {
       setSelectedItem(item)
       await getFoodDonor(item.id)
            .then(response=>setdItems(response))
            .catch(error=>console.log(error))
    };

    const setdItems=(response)=>{
        setAddress(response.data.address)
        setName(response.data.name)
    }

    const closeModal=()=>{
        setSelectedItem(null)
        setAddress('')
        setName('')
    }

    const confirmOrder=()=>{
        console.log('confirmed')
        const food={
            id:selectedItem.id
        }
        orderFood(auth.id,food)
            .then(response=>console.log(response))
            .catch(error=>console.log(error))
        const newitems=items.filter(item=>item.id!==selectedItem.id)
        setItems(newitems)
        setSelectedItem(null)
        setAddress('')
        setName('')
    }

    return (
        <div className="food-list">
            {items.map((item) => (
                <FooditemCard
                    key={item.id}
                    image={item.image}
                    name={item.name}
                    expiryDate={item.expirydate}
                    quantity={item.quantity}
                    button='order  now'
                    onOrder={() => handleOrder(item)}
                />
            ))}
             {selectedItem && (
                <div className="modal-overlay">
                    <div className="modal">
                        <h2>Food Details</h2>
                        <p><strong>Name: </strong> {selectedItem.name}</p>
                        <p><strong>Quantity: </strong> {selectedItem.quantity} grams</p>
                        <p><strong>Expiry Date: </strong> {selectedItem.expirydate}</p>
                        <p><strong>Donor: </strong> {name}</p>
                        <p><strong>Collect it from: </strong> {address}</p>
                        <div className="modal-actions">
                            <button onClick={confirmOrder}>Confirm Order</button>
                            <button onClick={closeModal}>Cancel</button>
                        </div>
                    </div>
                </div>
            )}
        </div>
    )
}