import { useEffect, useState } from "react";
import { getFoodOrders, getFoods } from "../api/ApiService";
import { Pie } from "react-chartjs-2";
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from "chart.js";
import '../css/statistics.css'

ChartJS.register(ArcElement, Tooltip, Legend);

export default function Statistics() {
    const [savedAmount, setSavedAmount] = useState(0);
    const [donatedAmount, setDonatedAmount] = useState(0);
    const [wastedAmount, setwastedAmount] = useState(0)

    useEffect(() => {
        async function fetchData() {
            try {
                const foodResponse = await getFoods();

                let totalDonated = 0;
                let totalSaved = 0;

                for (const food of foodResponse.data) {
                    totalDonated += food.quantity;

                    const orderResponse = await getFoodOrders(food.id);

                    if (orderResponse.data.length > 0) {
                        totalSaved += orderResponse.data[0].food.quantity;
                    }
                }

                setDonatedAmount(totalDonated);
                setSavedAmount(totalSaved);
                setwastedAmount(totalDonated - totalSaved)
            } catch (error) {
                console.log(error);
            }
        }

        fetchData();
    }, []);

    const data = {
        labels: ["Saved Food", "Wasted Food"],
        datasets: [
            {
                label: "Food Statistics (grams)",
                data: [savedAmount, wastedAmount],
                backgroundColor: ["green", "red"],
                hoverBackgroundColor: ["#64b5f6", "#ff9aa2"],
                borderWidth: 2,
            },
        ],
    };

    const options = {
        responsive: true,
        plugins: {
            legend: {
                position: "right",
                labels: {
                    font: {
                        size: 14,
                    },
                    color: "#4a4a4a",
                },
            },
            tooltip: {
                callbacks: {
                    label: (tooltipItem) =>
                        `${tooltipItem.label}: ${tooltipItem.raw} grams`,
                },
            },
        },
        elements: {
            arc: {
                borderWidth: 1,
            },
        },
        maintainAspectRatio: false,
    };

    return (
        <div  id="statistics" style={{ 
            display: "flex", 
            justifyContent: "center", 
            alignItems: "center", 
            textAlign: "center",
        }}>
            <div style={{ width: "50%", height: "400px" }}>
                <Pie data={data} options={options} />
            </div>
            <div style={{ width: "50%", paddingLeft: "20px" }}>
                <h2 style={{ fontSize: "22px", marginBottom: "15px", color: "blue" }}>Food Security Statistics</h2>
                <p style={{fontSize:"18px",marginBottom:"10px"}}><strong>Total Food Donated:</strong> {donatedAmount} grams</p>
                <p style={{fontSize:"18px",marginBottom:"10px"}}><strong>Total Food Saved:</strong> {savedAmount} grams</p>
                <p style={{fontSize:"18px"}}><strong>Total Food Wasted:</strong> {wastedAmount} grams</p>
            </div>
        </div>
    )
}