import { useEffect, useState } from 'react';
import { getDonations, getFoodOrders } from './api/ApiService';
import { useAuth } from './AuthContext';
import FooditemCard from './FooditemCard';

export default function Donationsmy(){
    const auth=useAuth()
    const [items,setItems]=useState([])
    const [selectedItem,setSelectedItem]=useState(null)
    const [name,setName]=useState('not ordered')
    const [address,setAddress]=useState('not ordered')

    useEffect(()=>{
        getDonations(auth.id)
            .then((response)=>setItems(response.data))
            .catch((error)=>console.log(error))
    })

    const onMoreDetails=async (item)=>{
        await getFoodOrders(item.id)
            .then(response=>setdItems(response))
            .catch(error=>console.log(error))
        setSelectedItem(item)
    }

    const setdItems=(response)=>{
        if(response.data.length!==0){
            setName(response.data[0].user.name)
            setAddress(response.data[0].user.address)
        }
    }

    const closeModal=()=>{
        setSelectedItem(null)
        setAddress('not ordered')
        setName('not ordered')
    }

    return(
        <div className="food-list">
            {items.map((item) => (
                <FooditemCard
                    key={item.id}
                    image={item.image}
                    name={item.name}
                    expiryDate={item.expirydate}
                    quantity={item.quantity}
                    button="more details"
                    onOrder={()=>onMoreDetails(item)}
                />
            ))}
            {selectedItem && (
                <div className="modal-overlay">
                    <div className="modal">
                        <h2>Food Details</h2>
                        <p><strong>Name: </strong> {selectedItem.name}</p>
                        <p><strong>Quantity: </strong> {selectedItem.quantity} grams</p>
                        <p><strong>Expiry Date: </strong> {selectedItem.expirydate}</p>
                        <p><strong>Ordered By: </strong> {name}</p>
                        <p><strong>Receipent Address: </strong> {address}</p>
                        <div className="modal-actions">
                            <button onClick={closeModal}>Cancel</button>
                        </div>
                    </div>
                </div>
            )}
        </div>
    )
}