import { useEffect, useState } from 'react'
import '../css/home.css'
import { getFoodOrders, getFoods } from '../api/ApiService'
export default function Home(){
    const [savedAmount,setSavedAmount]=useState(0)
    useEffect(()=>{
        async function fetchData() {
            try{
                const foodResponse=await getFoods()
                let saved=0
                for(const food of foodResponse.data){
                    const foodOrders=await getFoodOrders(food.id)
                    if(foodOrders.data.length>0){
                        saved+=foodOrders.data[0].food.quantity
                    }
                }
                setSavedAmount(saved)
            }
            catch(error){
                console.log(error)
            }
        }
        fetchData()
    },[])
    return(
        <div className='homec container' id='home'>
            <div className='hometxt'>
                <h1>Turning excess into access</h1>
                <h2>Together We've saved <strong style={{"color":"yellow"}}>{savedAmount}</strong> Grams of Food</h2>
                <p>
                Empowering communities to share their surplus food and ensure nothing goes to waste.
                By working as one, we reduce waste and ensure that no one goes hungry in our neighborhoods.
                </p>
            </div>
        </div>
    )
}