import './css/foodItemcard.css';

export default function FooditemCard({ image, name, expiryDate,quantity,onOrder,button}) {
    return (
        <div className="food-card">
            <img src={image} alt={name} className="food-image" />
            <div className="food-info">
                <h3 className="food-name">{name}</h3>
                <p className="food-expiry">Expires on: {expiryDate}</p>
                <p className='food-expiry'>Quantity: {quantity} grams</p>
                <button className="order-button" onClick={onOrder}>{button}</button>
            </div>
        </div>
    )
}